body {
  margin: 2px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
* {
  font-family: "Verdana", cursive, sans-serif;
  color: #000002;
}

body {
  background-color: #39a0f0;
  display: flex;
  justify-content: center;
  align-items: center;

}

.app {
  background-color: #3da1cf;
  width: 45vw;
  height:40vh;
  position: relative;
  top:25vh;
  margin-left:25%;
  border-radius: 15px;
  padding: 20px;
  box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
  display: flex;
  justify-content: space-evenly;
  font-size: 1.4  em;

}

.chord {
 

  position: relative;

  border-radius: 6px;
  padding: 10px;
  margin-bottom:10px;
  box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
  display: flex;
  justify-content: space-evenly;
}


.scale {
 

  position: relative;
  width:30vw;
  border-radius: 6px;
  padding: 4  px;
  margin-bottom:10px;
  box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
  display: flex;
  justify-content: space-evenly;
}

.sound {
 

  position: relative;
  margin:auto;
  border-radius: 6px;
  padding: 10px;
  margin-bottom:3px;
  box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
  top:-7vh;
  height:5vh;
  width:47vw;

}

h1{
  color:#5fb2f1;
  margin-left:3vw;
}

h2{
  color:#5fb2f1;
  margin-left:5vw;  

}

h2{
  color:#001424;
  margin-left:5vw;

}

.Nav {
  height: 6em;
  color:#5fb2f1;
  font-size: 24px;
  align-items: center;
  background-color: rgb(111, 125, 138);
  width:90vw;
  border-radius: 15px;;
}

.score-section {
  display: flex;
  font-size: 24px;
  align-items: center;
}


.question-section {
  width: 100%;
  position: relative;
}

.question-count {
  margin-bottom: 20px;
}

.question-count span {
  font-size: 28px;
}

.question-text {
  margin-bottom: 12px;
}

.timer-text {
  background: rgb(230, 153, 12);
  padding: 15px;
  margin-top: 20px;
  margin-right: 20px;
  border: 5px solid rgb(255, 189, 67);
  border-radius: 15px;
  text-align: center;
}


.answer-section {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

button {
  width: 100%;
  font-size: 24px;
  color: #0e0d0d;
  background-color: #83c5eb;
  border-radius: 15px;
  display: flex;
  padding: 5px;
  justify-content: flex-start;
  align-items: center;
  border: 5px solid #234668;
  cursor: pointer;
}

.correct {
  background-color: #2f922f;
}

.incorrect {
  background-color: #ff3333;
}

button:hover {
  background-color: #555e7d;
}

button:focus {
  outline: none;
}

button svg {
  margin-right: 5px;
}